.movie {
  cursor: pointer;
}

.particles {
  position: fixed;
  right: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.pageNumbers{
  list-style: none;
  display: flex;
}

.pageNumbers li {
  padding: 10px;
  border: 1px solid #fff;
  cursor: pointer;
}

.pageNumbers li.active {
  background-color: #fff;
  color: #000;
}

.pagination {
  margin: 15px auto;
  display: flex;
  list-style: none;
  outline: none;
}
.pagination > .active > a{
  background-color: #47ccde ;
  border-color: #47ccde ;
  color: #fff;
}
.pagination > li > a{
  border: 1px solid #47ccde ;
  padding: 5px 10px;
  outline: none;
  cursor: pointer;
  color: #fff;
}
.pagination > .active > a, .pagination > .active > span, .pagination > .active > a:hover, .pagination > .active > span:hover, .pagination > .active > a:focus, .pagination > .active > span:focus{
  background-color: #47ccde ;
  border-color: #47ccde;
  outline: none;
  color: #fff;
}
.pagination > li > a, .pagination > li > span{
  color: #47ccde
}
.pagination > li:first-child > a, .pagination > li:first-child > span, .pagination > li:last-child > a, .pagination > li:last-child > span{
  border-radius: unset;
  -webkit-border-radius: unset;
  -moz-border-radius: unset;
  -ms-border-radius: unset;
  -o-border-radius: unset;
  color: #fff;

}

.pagination > li.paginationDisabled  {
display: none;
}

.section-content {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 120vh;
    position: relative;
    background-size: cover;
}
.movie-detail-content {
    position: absolute;
    left: 10%;
    width: 80vw;
    background-color: rgba(0, 0, 0, 0.5);
}

.movie-title , .realse-date , .vount-count{
    color: #ffA500;
}
.movie-title {
    font-size: 60px;
}
.movie-tagline {
    color: #f3db00;
    text-transform: uppercase;
    font-size: 25px;
}
.movie-desc {
    line-height: 1.6;
    color: #fff;
}
.image {
    box-shadow: 10px 10px 5px #000;
}
.realse-date , .vount-count {
    font-size: 20px;
    display: block;
}
.cast-star , .official-star {
    color: #f1d9aa;
}

.cast-item , .official-trailer{
    color: orange;
    font-size: 30px;
    letter-spacing: 1.2px;
}

.trailer {
    width: 80vw;
    height: 80vh;
}
